import React, { useContext, useEffect } from "react";
import { useState } from "react";
import { Tooltip, Grow } from "@mui/material";

// import { watchlist } from "../data/data";
import { DoughnutChart } from "./DoughnoutChart";
import {
  BarChartOutlined,
  KeyboardArrowDown,
  KeyboardArrowUp,
  MoreHoriz,
} from "@mui/icons-material";
import GeneralContext from "./GeneralContext";
import axios from "axios";

export default function Watchlist() {
  const [stocks, setStocks] = useState([]);
  const watchSymbols = [
    "INFY",
    "WIT",
    "MSFT",
    "AMZN",
    "GOOGL",
    "META",
    "TSLA",
    "AAPL",
    "JPM",
    "JNJ",
    "NFLX",
    "	BRK.B",
  ];

  useEffect(() => {
    const fetchData = async () => {
      try {
        const responses = await Promise.all(
          watchSymbols.map((symbol) =>
            // axios.get(`http://localhost:3002/api/stocks/daily/${symbol}`)
            axios.get(`https://tradeeasy.onrender.com/getData/${symbol}`)
          )
        );
        const stocksData = responses.map((res, index) => res.data);
        setStocks(stocksData);
      } catch (error) {
        console.error("Error fetching stock data:", error);
      }
    };

    fetchData();
  }, []);

  const labels = stocks.map((subArray) => subArray["name"]);

  const data = {
    //   // labels: ['Red', 'Blue', 'Yellow', 'Green', 'Purple', 'Orange'],

    labels,
    datasets: [
      {
        label: "Price",
        data: stocks.map((stock) => stock.price),
        backgroundColor: [
          "rgba(255, 99, 132, 0.5)",
          "rgba(54, 162, 235, 0.5)",
          "rgba(255, 206, 86, 0.5)",
          "rgba(75, 192, 192, 0.5)",
          "rgba(153, 102, 255, 0.5)",
          "rgba(255, 159, 64, 0.5)",
        ],
        borderColor: [
          "rgba(255, 99, 132, 1)",
          "rgba(54, 162, 235, 1)",
          "rgba(255, 206, 86, 1)",
          "rgba(75, 192, 192, 1)",
          "rgba(153, 102, 255, 1)",
          "rgba(255, 159, 64, 1)",
        ],
        borderWidth: 1,
      },
    ],
  };
  return (
    <div className="watchlist-container">
      <div className="search-container">
        <input
          type="text"
          name="search"
          id="search"
          placeholder="Search eg:infy, bse, nifty fut weekly, gold mcx"
          className="search"
        />
        <span className="counts"> {stocks.length} / 50</span>
      </div>

      <ul className="list">
        {/* {console.log(stocks)} */}
        {stocks.map((stock, index) => {
          return (
            <>
              <WatchlistItem stock={stock} key={index} />
            </>
          );
        })}
      </ul>
      <DoughnutChart data={data} />
    </div>
  );
}

const WatchlistItem = ({ stock }) => {
  const [showWatchListActions, setShowWatchListActions] = useState(false);

  const handleMouseEnter = (e) => {
    setShowWatchListActions(true);
  };
  const handleMouseLeave = (e) => {
    setShowWatchListActions(false);
  };

  return (
    <>
      <li onMouseEnter={handleMouseEnter} onMouseLeave={handleMouseLeave}>
        <div className="item">
          <p className={stock.isDown ? "down" : "up"}>{stock.name}</p>
          <div className="itemInfo">
            <span className="percent">{stock.percent}</span>
            {stock.isDown ? (
              <KeyboardArrowDown className="down" />
            ) : (
              <KeyboardArrowUp className="up" />
            )}
            <span className="price" style={{ color: "white" }}>
              {stock.price}
            </span>
          </div>
        </div>
        {showWatchListActions && (
          <WatchlistActions uid={stock.name} price={stock.price} />
        )}
      </li>
    </>
  );
};

const WatchlistActions = ({ uid, price, qty }) => {
  const generalContext = useContext(GeneralContext);

  const handleBuyClick = () => {
    generalContext.openBuyWindow(uid, price);
  };
  const handleSellClick = () => {
    generalContext.openSellWindow(uid, price);
  };
  return (
    <span className="actions">
      <span>
        <span>
          <Tooltip
            title="Buy(B)"
            placement="top"
            arrow
            TransitionComponent={Grow}
          >
            <button className="buy" onClick={handleBuyClick}>
              Buy
            </button>
          </Tooltip>
          <Tooltip
            title="Sell(S)"
            placement="top"
            arrow
            TransitionComponent={Grow}
          >
            <button className="sell" onClick={handleSellClick}>
              Sell
            </button>
          </Tooltip>

          <Tooltip
            title="Analytics(A)"
            placement="top"
            arrow
            TransitionComponent={Grow}
          >
            <button className="action">
              <BarChartOutlined className="icon" />
            </button>
          </Tooltip>
          <Tooltip
            title="more"
            placement="top"
            arrow
            TransitionComponent={Grow}
          >
            <button className="action">
              <MoreHoriz className="icon" />
            </button>
          </Tooltip>
        </span>
      </span>
    </span>
  );
};
